<template>
  <div class="orderDetail">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="wrap">
        <div class="wrap1">
          <div class="address">
            <p class="left"></p>
            <p class="right"></p>
          </div>
          <div class="address">
            <p class="left"></p>
            <p class="right">{{adress}}</p>
          </div>
          <div class="address">
            <p class="left"></p>
            <p class="right">{{time}}</p>
          </div>
          <div class="address telbox">
            <p class="left"></p>
            <p class="right tel">13865785412</p>
          </div>
        </div>

        <div class="wrap2">
          <div class="address">
            <p class="left"></p>
            <p class="right">{{ifType}}</p>
          </div>
          <div class="title">
            <p class="title1">/</p>
            <i></i>
          </div>

          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eLife}}</span>/<span>{{fLife}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eWater}}</span>/<span>{{fWater}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eSweeping}}</span>/<span>{{fSweeping}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eFood}}</span>/<span>{{fFood}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eBurnCinder}}</span>/<span>{{fBurnCinder}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{ePlastic}}</span>/<span>{{fPlastic}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eGungo}}</span>/<span>{{fGungo}}</span></p>
          </div>

          <div class="address total">
            <p class="left">()</p>
            <p class="right">{{amountGarbageForShow}}</p>
          </div>
        </div>       
      </div>

  </div>
</template>

<script>
import header from '@/components/Header/Header'
export default {
  data () {
    return {
      msg: '',
      re: '1',
      adress: '',
      time: '',
      ifType: '',
      eBurnCinder: '',
      eFood: '',
      eLife: '',
      ePlastic: '',
      eSweeping: '',
      eWater: '',
      fBurnCinder: '',
      fFood: '',
      fLife: '',
      fPlastic: '',
      fSweeping: '',
      fWater: '',
      fGungo: '',
      eGungo: '',
      amountGarbageForShow: ''
    }
  },
  components: {
    'v-header': header
  },
  computed: {
    total: function () {
      let sum = 0
      if (this.a > 0) {
        sum += this.a
      } else {
        sum += this.A
      }
      if (this.b > 0) {
        sum += this.b
      } else {
        sum += this.B
      }
      if (this.c > 0) {
        sum += this.c
      } else {
        sum += this.C
      }
      if (this.d > 0) {
        sum += this.d
      } else {
        sum += this.D
      }
      if (this.e > 0) {
        sum += this.e
      } else {
        sum += this.E
      }
      if (this.f > 0) {
        sum += this.f
      } else {
        sum += this.F
      }
      return sum
    }
  },
  mounted () {
    this.details()
  },
  methods: {
    details: function () {
      var _this = this
      var customerShipName = sessionStorage.getItem('customerShipName')
      var adress = sessionStorage.getItem('adress')
      var time = sessionStorage.getItem('time')
      var ifType = sessionStorage.getItem('ifType')
      var eBurnCinder = sessionStorage.getItem('eBurnCinder')
      var eFood = sessionStorage.getItem('eFood')
      var eLife = sessionStorage.getItem('eLife')
      var ePlastic = sessionStorage.getItem('ePlastic')
      var eSweeping = sessionStorage.getItem('eSweeping')
      var eWater = sessionStorage.getItem('eWater')
      var eGungo = sessionStorage.getItem('eGungo')
      var amountGarbageForShow = sessionStorage.getItem('amountGarbageForShow')
      _this.adress = adress
      _this.time = time
      _this.msg = customerShipName
      _this.ifType = ifType
      _this.eBurnCinder = eBurnCinder
      _this.eFood = eFood
      _this.eLife = eLife
      _this.ePlastic = ePlastic
      _this.eSweeping = eSweeping
      _this.eWater = eWater
      _this.eGungo = eGungo
      _this.amountGarbageForShow = amountGarbageForShow
      var fBurnCinder = sessionStorage.getItem('fBurnCinder')
      var fFood = sessionStorage.getItem('fFood')
      var fLife = sessionStorage.getItem('fLife')
      var fPlastic = sessionStorage.getItem('fPlastic')
      var fSweeping = sessionStorage.getItem('fSweeping')
      var fWater = sessionStorage.getItem('fWater')
      var fGungo = sessionStorage.getItem('fGungo')
      var amountGarbage = sessionStorage.getItem('amountGarbage')
      _this.fBurnCinder = fBurnCinder
      _this.fFood = fFood
      _this.fLife = fLife
      _this.fPlastic = fPlastic
      _this.fSweeping = fSweeping
      _this.fWater = fWater
      _this.fGungo = fGungo
      _this.amountGarbage = amountGarbage
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrap{
    background: #f7f8fa;
    margin-top: 1.173333rem;
    overflow: scroll;
    height: 16.7rem;
  }
  .wrap1{
    width: 100%;
    padding-left: 0.533rem;
    padding-right: 0.533rem;
    border-bottom: 0.027rem #ccc solid;
    box-sizing: border-box;
    background: #fff;
  }
  .wrap2{
    width: 100%;
    height:11.05rem;
    padding-left: 0.533rem;
    padding-right: 0.533rem;
    border-top: 0.027rem #ccc solid;
    border-bottom: 0.027rem #ccc solid;
    box-sizing: border-box;
    background: #fff;
    margin-top: 0.267rem;
  }
  .wrap2 .address,.wrap2 .title{
    height: 1.2rem;
    line-height: 1.2rem;
  }
  .wrap2 .address p,.wrap2 .title p{
    height: 1.2rem;
    line-height: 1.2rem;
    line-height: 1.2rem;
  }
  .address{
    width: 100%;
    height: 1.333rem;
    border-bottom:1px solid #ccc; 
  }
  .title{
    width: 100%;
    height: 1.333rem;
  }
  .address .left{
    font-size: 0.453rem;
    float: left;
    line-height: 1.333rem;
  }
  .address .right{
    font-size: 0.4rem;
    float: right;
    line-height: 1.333rem;
    text-align: right;
  }
  .address .tel{
    color: #2296f2;
    font-size: 0.453rem;
    background: url('icon_list_Telephone.png') no-repeat;
    background-position: 0 center;
    background-size: 0.36rem 0.4rem;
    padding-left: 0.4rem;
  }
  .telbox{
    border-bottom: none;
  }
  .title i{
    background: url('icon_kind_l.png');
    float: right;
    width: 0.32rem;
    height: 0.347rem;
    margin-top: 0.43rem;
    margin-right: 0.16rem;
  }
  .title1{
    font-size: 0.4rem;
    float: right;
    line-height: 1.333rem;
    text-align: right;
    color: #2296f2;
    float: right;
  }
  .total{
    border-bottom: none;
  }
  .total{
    color: #fc1b1b;
  }
  .total .right{
    font-size: 0.453rem;
  }
</style>



