<template>
  <div class="alert" v-show='flag'>
    <div class="box" :class='{box1:btn=="1"}'>
      <div class="content1">{{msga}}</div>
      <div class="content2" v-show='tel'>{{tel}}</div>
      <div class="btnbox">
        <div class="btnleft" v-if='btn==0'></div>
        <div class="btnleft1" v-else></div>
        <div class="btnright" v-on:click='cancel'></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    flag: {
      type: Number
    },
    msga: {
      type: String
    },
    tel: {
      type: String
    },
    btn: {
      type: String
    }
  },
  data: function () {
    return {
      flag1: this.flag
    }
  },
  created: function () {
  },
  computed: {
  },
  methods: {
    cancel: function () {
      this.$emit('cancel')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .alert{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    position: absolute;
    top: 0;
    z-index: 10000;
  }
  .box{
    margin: 0 auto;
    margin-top: 4.933rem;
    width: 7.867rem;
    height: 5.333rem;
    background: #fff;
    border-radius: 3px;
    overflow: hidden;
  }
  .box1{
    height: 5.333333rem;
  }
  .content1{
    font-size: 0.4rem;
    color: #444;
    text-align: center;
    line-height: 0.4rem;
    margin-top: 1.0rem;
  }
  .content2{
    width: 100%;
    height: 0.64rem;
    font-size: 0.533333rem;
    color: #2396F3;
    font-weight: 600;
    margin-top: 0.587rem;
    background: url('icon_list_Telephone.png') no-repeat;
    background-size: 0.4rem 0.48rem;
    background-position: 1.819rem 0rem;
  }
  .btnbox{
    margin-top: 1.2rem;
    height: 0.907rem;
    width: 100%;
  }
  .btnleft{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_dial.png');
    background-size: 3.6rem 1.2rem;
    margin-left: 0.213rem;
    float: left;
  }
  .btnleft1{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_dial.png');
    background-size: 3.6rem 1.2rem;
    margin-left: 0.213rem;
    float: left;
  }
  .btnright{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_cancel.png');
    background-size: 3.6rem 1.2rem;
    margin-right: 0.213rem;
    float: right;
  }
</style>



