<template>
  <div class="statisticslist">
      <v-header :msg='msg' :re='re'></v-header>
       <div class="main">
         <div class="list" v-for="result in result" @click='orderdetails(result)'>
          <div class="titleship">{{result.customerShipName}}</div>
          <div class="location">{{result.adress}}</div>
          <div class="time">{{result.workDate}}</div>
          <div :class="rubbish+result.taskType">{{result.taskTypeName}}</div>
         </div>
       <div class="textb"></div>
       </div>
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import $ from 'jquery'
export default {
  data () {
    return {
      msg: '',
      re: '4',
      result: ''
    }
  },
  components: {
    'v-header': header
  },
  mounted () {
    this.listpage()
  },
  methods: {
    listpage: function () {
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '004002', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {name: sessionStorage.getItem('loginname'), difference: '0', pageNo: '1', pageSize: '100'}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: '11111'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e.msgEx.infos.results)
          _this.result = e.msgEx.infos.results
        },
        error: function (e) {
          console.log(e)
        }
      })
    },
    orderdetails: function (result) {
      console.log(result)
      sessionStorage.setItem('customerShipName', result.customerShipName)
      sessionStorage.setItem('adress', result.adress)
      sessionStorage.setItem('time', result.workDate)
      sessionStorage.setItem('customerShipId', result.customerShipId)
      sessionStorage.setItem('taskCode', result.taskCode)
      sessionStorage.setItem('taskType', result.taskType)
      if (result.ifType === '0') {
        sessionStorage.setItem('ifType', '')
      } else {
        sessionStorage.setItem('ifType', '')
      }
      if (result.eBurnCinder === '') {
        sessionStorage.setItem('eBurnCinder', '-')
      } else {
        sessionStorage.setItem('eBurnCinder', result.eBurnCinder)
      }
      if (result.eFood === '') {
        sessionStorage.setItem('eFood', '-')
      } else {
        sessionStorage.setItem('eFood', result.eFood)
      }
      if (result.eLife === '') {
        sessionStorage.setItem('eLife', '-')
      } else {
        sessionStorage.setItem('eLife', result.eLife)
      }
      if (result.ePlastic === '') {
        sessionStorage.setItem('ePlastic', '-')
      } else {
        sessionStorage.setItem('ePlastic', result.ePlastic)
      }
      if (result.eSweeping === '') {
        sessionStorage.setItem('eSweeping', '-')
      } else {
        sessionStorage.setItem('eSweeping', result.eSweeping)
      }
      if (result.eWater === '') {
        sessionStorage.setItem('eWater', '-')
      } else {
        sessionStorage.setItem('eWater', result.eWater)
      }
      if (result.fBurnCinder === '') {
        sessionStorage.setItem('fBurnCinder', '0')
      } else {
        sessionStorage.setItem('fBurnCinder', result.fBurnCinder)
      }
      if (result.fFood === '') {
        sessionStorage.setItem('fFood', '0')
      } else {
        sessionStorage.setItem('fFood', result.fFood)
      }
      if (result.fLife === '') {
        sessionStorage.setItem('fLife', '0')
      } else {
        sessionStorage.setItem('fLife', result.fLife)
      }
      if (result.fPlastic === '') {
        sessionStorage.setItem('fPlastic', '0')
      } else {
        sessionStorage.setItem('fPlastic', result.fPlastic)
      }
      if (result.fSweeping === '') {
        sessionStorage.setItem('fSweeping', '0')
      } else {
        sessionStorage.setItem('fSweeping', result.fSweeping)
      }
      if (result.fWater === '') {
        sessionStorage.setItem('fWater', '0')
      } else {
        sessionStorage.setItem('fWater', result.fWater)
      }
      if (result.amountGarbageForShow === '') {
        sessionStorage.setItem('amountGarbageForShow', '0')
      } else {
        sessionStorage.setItem('amountGarbageForShow', result.amountGarbageForShow)
      }
      if (result.amountGarbage === '') {
        sessionStorage.setItem('amountGarbage', '-')
      } else {
        sessionStorage.setItem('amountGarbage', result.amountGarbage)
      }
      if (result.eGungo === '') {
        sessionStorage.setItem('eGungo', '-')
      } else {
        sessionStorage.setItem('eGungo', result.eGungo)
      }
      if (result.fGungo === '') {
        sessionStorage.setItem('fGungo', '0')
      } else {
        sessionStorage.setItem('fGungo', result.fGungo)
      }
      this.$router.push({ path: '/orderfinish' })
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'background:#f7f8fa')
  },
  beforeDestroy () {
    document.querySelector('body').setAttribute('style', '')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.body{
  height: 100%;
  width: 100%;
}
.main{
  position: fixed;
  width: 100%;
  height: 94%;
  padding-top: 1.173333rem;
  overflow: scroll;
}
.list{
  width: 9.6rem;
  height: 1.733333rem;
  background: #ffffff;
  margin: 0 auto;
  margin-top: 0.133333rem;
  border: 1px solid #cbd9e6;
}

.titleship{
  width: 50%;
  float: left;
  font-size: 0.453333rem;
  font-weight: bold;
  margin-top: 0.4rem;
  padding-left: 0.826667rem;
  text-align: left;
  background: url(icon_ship.png) no-repeat;
  background-position-x:0.293333rem;
}

.titleport{
  width: 50%;
  float: left;
  font-size: 0.453333rem;
  font-weight: bold;
  margin-top: 0.4rem;
  padding-left: 0.826667rem;
  text-align: left;
  background: url(icon_Port.png) no-repeat;
  background-position-x:0.293333rem;
}

.location{
  float: left;
  width: 50%;
  font-size: 0.346667rem;
  color: #444444;
  margin-top: 0.186667rem;
  text-align: left;
  padding-left: 0.826667rem;
}

.time{
  margin-top: 0.4rem;
  text-align: right;
  margin-right: 0.4rem;
  font-size: 0.32rem;
  color: #666666;
}

.undefined0{
  float: right;
  font-size: 0.4rem;
  color: #2296f2;
  margin-right: 0.453333rem;
  line-height: 0.59rem;
  padding-left: 0.4rem;
  background: url(icon_garbage.png) no-repeat;
  background-position-y: center;
}
.undefined1{
  float: right;
  font-size: 0.4rem;
  color: #56ac3c;
  margin-right: 0.453333rem;
  line-height: 0.59rem;
  padding-left: 0.4rem;
  background: url(icon_water.png) no-repeat;
  background-position-y: center;
}
.undefined2{
  float: right;
  font-size: 0.4rem;
  color: #f39704;
  margin-right: 0.453333rem;
  line-height: 0.59rem;
  padding-left: 0.4rem;
  background: url(icon_Flood.png) no-repeat;
  background-position-y: center;
}
.undefined3{
  float: right;
  font-size: 0.4rem;
  color: #f34704;
  margin-right: 0.453333rem;
  line-height: 0.59rem;
  padding-left: 0.4rem;
  background: url(icon_urgent.png) no-repeat;
  background-position-y: center;
}

.textb{
  margin-top: 0.4rem;
  font-size: 0.32rem;
  color: #eeeeee;
}
</style>



