<template>
    <div class="index1">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="cityBG">
        <div class="dateModel">
          <div class="leftBorder">
          <div class="leftTag">
          </div></div><div class="month">
          {{attribte1}}/</div><div class="day">
          {{totalRecord}}</div><div class="rightBorder"><div class="rightTag">
          </div></div>
        </div>
        <div class="bottomBg">
        </div>
      </div>
      <div class="filler">
      </div>
      <div id="worklist">
        <div class="shipItem" id="firstitem">
        <div class="leftTime"><div class="timeText">
        {{results.showTime}}</div>
        </div>
        <div class="shipInfo">
          <div class="firstTipLeft">
            <div class="shipimg"></div><div class="shipName">
            {{results.customerShipName}}</div><div><div class="shipIsland">
            {{results.adress}}</div></div> 
          </div>
          <div class="firstTipRight">
            <div class="checkButton" @click='orderdetails(results)'></div>
            <div class="checkState">{{results.taskStatusName}}</div>
          </div>
        </div>
      </div>
        <div class="shipItem" v-for="result in result">
        <div class="leftTime"><div class="timeText">
        {{result.showTime}}</div>
        </div>
        <div class="shipInfob">
          <div class="firstTipLeft">
            <div class="shipimg2"></div><div class="shipName2">
            {{result.customerShipName}}</div><div class="addr2"><div class="shipIsland2">
            {{result.adress}}</div></div> 
          </div>
          <div class="firstTipRight">
            <div class="checkButton" @click='orderdetails(result)'></div>
            <div class="checkStateing">{{result.taskStatusName}}</div>
          </div>
        </div>
      </div>
      </div>
      <router-link to="createOrder">
      <div id="creatework"></div>
        </router-link>
      <v-bottom></v-bottom>
    </div>
</template>

<script>
  import header from '@/components/Header/Header'
  import bottom from '@/components/bottom/bottom'
  import $ from 'jquery'
  export default {
    data () {
      return {
        msg: '',
        re: '0',
        results: '',
        result: '',
        totalRecord: '0',
        attribte1: '0'
      }
    },
    components: {
      'v-header': header,
      'v-bottom': bottom
    },
    mounted () {
      this.orderlist()
    },
    methods: {
      orderlist: function () {
        var _this = this
        $.ajax({
          type: 'post',
          url: '/smi-WSApp/Api/requestDeal',
          data: {arg0: '004002', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {name: sessionStorage.getItem('loginname'), difference: '1', pageNo: 1, pageSize: 20}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
          dataType: 'json',
          success: function (e) {
            var listpage = (e.msgEx.infos.results)
            console.log(listpage)
            if (listpage.length === 0) {
              $('#firstitem').addClass('dis')
            } else {
              $('#firstitem').removeClass('dis')
            }
            _this.results = listpage[0]
            console.log(listpage.slice(1))
            _this.result = listpage.slice(1)
            _this.totalRecord = e.msgEx.infos.totalRecord
            _this.attribte1 = e.msgEx.infos.attribte1
          },
          error: function (e) {
            console.log(e)
          }
        })
      },
      orderdetails: function (result) {
        console.log(result)
        sessionStorage.setItem('customerShipName', result.customerShipName)
        sessionStorage.setItem('adress', result.adress)
        sessionStorage.setItem('time', result.workDate)
        sessionStorage.setItem('customerShipId', result.customerShipId)
        sessionStorage.setItem('taskCode', result.taskCode)
        sessionStorage.setItem('taskType', result.taskType)
        if (result.ifType === '0') {
          sessionStorage.setItem('ifType', '')
        } else {
          sessionStorage.setItem('ifType', '')
        }
        if (result.eBurnCinder === '') {
          sessionStorage.setItem('eBurnCinder', '-')
        } else {
          sessionStorage.setItem('eBurnCinder', result.eBurnCinder)
        }
        if (result.eFood === '') {
          sessionStorage.setItem('eFood', '-')
        } else {
          sessionStorage.setItem('eFood', result.eFood)
        }
        if (result.eLife === '') {
          sessionStorage.setItem('eLife', '-')
        } else {
          sessionStorage.setItem('eLife', result.eLife)
        }
        if (result.ePlastic === '') {
          sessionStorage.setItem('ePlastic', '-')
        } else {
          sessionStorage.setItem('ePlastic', result.ePlastic)
        }
        if (result.eSweeping === '') {
          sessionStorage.setItem('eSweeping', '-')
        } else {
          sessionStorage.setItem('eSweeping', result.eSweeping)
        }
        if (result.eWater === '') {
          sessionStorage.setItem('eWater', '-')
        } else {
          sessionStorage.setItem('eWater', result.eWater)
        }
        if (result.fBurnCinder === '') {
          sessionStorage.setItem('fBurnCinder', '0')
        } else {
          sessionStorage.setItem('fBurnCinder', result.fBurnCinder)
        }
        if (result.fFood === '') {
          sessionStorage.setItem('fFood', '0')
        } else {
          sessionStorage.setItem('fFood', result.fFood)
        }
        if (result.fLife === '') {
          sessionStorage.setItem('fLife', '0')
        } else {
          sessionStorage.setItem('fLife', result.fLife)
        }
        if (result.fPlastic === '') {
          sessionStorage.setItem('fPlastic', '0')
        } else {
          sessionStorage.setItem('fPlastic', result.fPlastic)
        }
        if (result.fSweeping === '') {
          sessionStorage.setItem('fSweeping', '0')
        } else {
          sessionStorage.setItem('fSweeping', result.fSweeping)
        }
        if (result.fWater === '') {
          sessionStorage.setItem('fWater', '0')
        } else {
          sessionStorage.setItem('fWater', result.fWater)
        }
        if (result.amountGarbageForShow === '') {
          sessionStorage.setItem('amountGarbageForShow', '0')
        } else {
          sessionStorage.setItem('amountGarbageForShow', result.amountGarbageForShow)
        }
        if (result.amountGarbage === '') {
          sessionStorage.setItem('amountGarbage', '-')
        } else {
          sessionStorage.setItem('amountGarbage', result.amountGarbage)
        }
        if (result.eGungo === '') {
          sessionStorage.setItem('eGungo', '-')
        } else {
          sessionStorage.setItem('eGungo', result.eGungo)
        }
        if (result.fGungo === '') {
          sessionStorage.setItem('fGungo', '0')
        } else {
          sessionStorage.setItem('fGungo', result.fGungo)
        }
        if (result.taskStatus === '1') {
          this.$router.push({ path: '/orderDetail' })
        }
        if (result.taskStatus === '2') {
          this.$router.push({ path: '/overwork' })
        }
        if (result.taskStatus === '4') {
          this.$router.push({ path: '/orderfinish' })
        }
        if (result.taskStatus === '5') {
          this.$router.push({ path: '/orderfinish' })
        }
        if (result.taskStatus === '3') {
          this.$router.push({ path: '/orderfinish' })
        }
      }
    }
  }
</script>

