<template>
  <div class="hello">
    <a class="return" href="javascript:history.back();" v-if='re==1'></a>
    <router-link to="order">
    <a class="return" v-if='re==4'></a>
    </router-link>
    <router-link to="index1">
    <a class="return" v-if='re==5'></a>
    </router-link>
    <router-link to="index1">
    <a class="return" v-if='re==6'></a>
    </router-link>
    <h1>{{ msg }}</h1>
     <router-link to="statisticslist">
    <a class="list" v-if='re==3'></a>
        </router-link>
    <div class="cancelorder" @click='show' v-if='re==6'></div>
    <v-appraise :flag='flag' :msga='msga' btn='3' v-on:cancel='Pcancel'></v-appraise>
  </div>
</template>

<script>
import appraise from '@/components/appraise/appraise'
export default {
  data () {
    return {
      flag: 0,
      msga: '?'
    }
  },
  props: {
    msg: {
      type: String
    },
    re: {
      type: String
    }
  },
  components: {
    'v-appraise': appraise
  },
  methods: {
    show: function () {
      this.flag = 1
    },
    Pcancel: function () {
      this.flag = 0
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  height: 44px;
  width: 100%;
  line-height: 44px;
  font-size: 17px;
  background: #53A7FF;
  color:#fff;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1001;
}
.hello .return{
  display: block;
  width: 8px;
  height: 15px;
  background: url('head_icon_Return.png');
  background-size: 8px 15px;
  background-repeat: no-repeat;
  position: absolute;
  top: 15px;
  left: 20px;
}

.list{
  display: block;
  width: 25px;
  height: 16px;
  background: url('btn_list.png');
  background-size: 25px 16px;
  background-repeat: no-repeat;
  position: absolute;
  top: 15px;
  left: 8.8rem;
}
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.cancelorder{
  font-size: 0.453333rem;
  color: #ffffff;
  margin-top: -1.173333rem;
  margin-left: 7.733333rem;
}
</style>



