<template>
  <div class="orderSure">
    <v-header :msg='msg' :re='re'></v-header>
    <div class="box">
    	<div class="address">
    		<div class="left"></div>
    		<div class="right gangkou">
          <span id="gkname">{{gk1}}</span>
    		</div>
    	</div>

    	<div class="country isdone">
    		<div class="left"></div>
    		<div class="right checkbox">
    			<p style='float:left' id="yyy" @click='change1' v-bind:class="{'yes':isbox,'no':!isbox}"></p>
    			<p style='float:left'  id="nnn" @click='change2' v-bind:class="{'yes':!isbox,'no':isbox}"></p>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='0' @click='add($event)'></div>
    			<input id="flife" class="icenter" type="Number" name="" v-model="A">
    			<div class="iright" data='0' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='1' @click='add($event)'></div>
    			<input id="fWater" class="icenter" type="Number" name="" v-model="B">
    			<div class="iright" data='1' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='2' @click='add($event)'></div>
    			<input id="fSweeping" class="icenter" type="Number" name="" v-model="C">
    			<div class="iright" data='2' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='3' @click='add($event)'></div>
    			<input id="fFood" class="icenter" type="Number" name="" v-model="D">
    			<div class="iright" data='3' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='4' @click='add($event)'></div>
    			<input id="fBurnCinder" class="icenter" type="Number" name="" v-model="E">
    			<div class="iright" data='4' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='5' @click='add($event)'></div>
    			<input id="fPlastic" class="icenter" type="Number" name="" v-model="F">
    			<div class="iright" data='5' @click='sub($event)'></div>
    		</div>
    	</div>
      <div class="sh">
        <div class="left">()</div>
        <div class="right sh1"> 
          <div class="ileft" data='6' @click='add($event)'></div>
          <input id="fGungo" class="icenter" type="Number" name="" v-model="G">
          <div class="iright" data='6' @click='sub($event)'></div>
        </div>
      </div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right total"> 
    			{{Math.floor((A+B+C+D+E+F+G) * 10) / 10}}
    		</div>
    	</div>
    <span id="amountGarbage" style="display:none">{{Math.floor((A+B+C+D+E+F+G) * 10) / 10}}</span>
    </div>
    <div class="btnbox">
    	<div class="surebtn" @click='subcreat'></div>
    </div>
    
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import $ from 'jquery'
export default {
  data () {
    return {
      msg: '',
      re: '1',
      id: '',
      tog: false,
      gk1: '',
      isbox: true,
      A: 0,
      B: 0,
      C: 0,
      D: 0,
      E: 0,
      F: 0,
      G: 0
    }
  },
  components: {
    'v-header': header
  },
  mounted () {
    this.searchA()
  },
  watch: {
    A: function () {
      if (this.A === 0) {
      } else {
        this.A = Math.floor(this.A * 10) / 10
      }
    },
    B: function () {
      if (this.B === 0) {
      } else {
        this.B = Math.floor(this.B * 10) / 10
      }
    },
    C: function () {
      if (this.C === 0) {
      } else {
        this.C = Math.floor(this.C * 10) / 10
      }
    },
    D: function () {
      if (this.D === 0) {
      } else {
        this.D = Math.floor(this.D * 10) / 10
      }
    },
    E: function () {
      if (this.E === 0) {
      } else {
        this.E = Math.floor(this.E * 10) / 10
      }
    },
    F: function () {
      if (this.F === 0) {
      } else {
        this.F = Math.floor(this.F * 10) / 10
      }
    },
    G: function () {
      if (this.G === 0) {
      } else {
        this.G = Math.floor(this.G * 10) / 10
      }
    }
  },
  methods: {
    toggle: function () {
      this.tog = !this.tog
    },
    chose: function (e, index) {
      this.gk1 = e.currentTarget.innerHTML
      this.tog = !this.tog
    },
    change1: function () {
      this.isbox = true
    },
    change2: function () {
      this.isbox = false
    },
    add: function (e) {
      var _this = e.currentTarget
      var aa = Number(_this.getAttribute('data'))
      if (aa === 0) {
        this.A = Number(this.A) + 0.5
      } else if (aa === 1) {
        this.B = Number(this.B) + 0.5
      } else if (aa === 2) {
        this.C = Number(this.C) + 0.5
      } else if (aa === 3) {
        this.D = Number(this.D) + 0.5
      } else if (aa === 4) {
        this.E = Number(this.E) + 0.5
      } else if (aa === 5) {
        this.F = Number(this.F) + 0.5
      } else if (aa === 6) {
        this.G = Number(this.G) + 0.5
      }
    },
    sub: function (e) {
      var _this = e.currentTarget
      var aa = Number(_this.getAttribute('data'))
      if (aa === 0 && this.A > 0) {
        this.A = Number(this.A) - 0.5
      } else if (aa === 1 && this.B > 0) {
        this.B = Number(this.B) - 0.5
      } else if (aa === 2 && this.C > 0) {
        this.C = Number(this.C) - 0.5
      } else if (aa === 3 && this.D > 0) {
        this.D = Number(this.D) - 0.5
      } else if (aa === 4 && this.E > 0) {
        this.E = Number(this.E) - 0.5
      } else if (aa === 5 && this.F > 0) {
        this.F = Number(this.F) - 0.5
      } else if (aa === 6 && this.G > 0) {
        this.G = Number(this.G) - 0.5
      }
    },
    searchA: function () {
      var customerShipName = sessionStorage.getItem('customerShipName')
      var _this = this
      _this.msg = customerShipName
      _this.gk1 = sessionStorage.getItem('adress')
      if (sessionStorage.getItem('ifType') === '') {
        $('#yyy').removeClass('no')
        $('#yyy').addClass('yes')
        $('#nnn').removeClass('yes')
        $('#nnn').addClass('no')
      }
      if (sessionStorage.getItem('ifType') === '') {
        $('#nnn').removeClass('no')
        $('#nnn').addClass('yes')
        $('#yyy').removeClass('yes')
        $('#yyy').addClass('no')
      }
      if (sessionStorage.getItem('eLife') === '-') {
        _this.A = 0
      } else {
        _this.A = parseInt(sessionStorage.getItem('eLife'))
      }
      if (sessionStorage.getItem('eWater') === '-') {
        _this.B = 0
      } else {
        _this.B = parseInt(sessionStorage.getItem('eWater'))
      }
      if (sessionStorage.getItem('eSweeping') === '-') {
        _this.C = 0
      } else {
        _this.C = parseInt(sessionStorage.getItem('eSweeping'))
      }
      if (sessionStorage.getItem('eFood') === '-') {
        _this.D = 0
      } else {
        _this.D = parseInt(sessionStorage.getItem('eFood'))
      }
      if (sessionStorage.getItem('eBurnCinder') === '-') {
        _this.E = 0
      } else {
        _this.E = parseInt(sessionStorage.getItem('eBurnCinder'))
      }
      if (sessionStorage.getItem('ePlastic') === '-') {
        _this.F = 0
      } else {
        _this.F = parseInt(sessionStorage.getItem('ePlastic'))
      }
      if (sessionStorage.getItem('eGungo') === '-') {
        _this.G = 0
      } else {
        _this.G = parseInt(sessionStorage.getItem('eGungo'))
      }
    },
    subcreat: function () {
      console.log($('.yes').text())
      var isf
      if ($('.yes').text() === '') {
        isf = 1
      } else {
        isf = 0
      }
      var myDate = new Date()
      var str = ''
      str += myDate.getFullYear() + '-'
      if (myDate.getMonth() >= 0 && myDate.getMonth() <= 9) {
        str += '0'
      }
      str += myDate.getMonth() + 1 + '-'
      str += myDate.getDate() + ' '
      str += myDate.getHours() + ':'
      if (myDate.getMinutes() >= 0 && myDate.getMinutes() <= 9) {
        str += '0'
      }
      str += myDate.getMinutes() + ':'
      if (myDate.getSeconds() >= 0 && myDate.getSeconds() <= 9) {
        str += '0'
      }
      str += myDate.getSeconds()
      console.log(str)
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '003005', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {id: sessionStorage.getItem('id'), userType: '2', taskCode: sessionStorage.getItem('taskCode'), customerShipId: sessionStorage.getItem('customerShipId'), workShipId: '', taskStatus: 4, taskType: sessionStorage.getItem('taskType'), adress: $('#gkname').text(), ifType: isf, fLife: $('#flife').val(), fSweeping: $('#fSweeping').val(), fFood: $('#fFood').val(), fBurnCinder: $('#fBurnCinder').val(), fPlastic: $('#fPlastic').val(), fWater: $('#fWater').val(), fGungo: $('#fGungo').val(), workDate: str, amountGarbageForShow: $('#amountGarbage').text(), createTime: str}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e)
          if (e.msgEx.respDesc === '!') {
            alert('!')
            _this.$router.push({ path: '/statisticslist' })
          } else {
            alert(e.msgEx.respDesc)
          }
        },
        error: function (e) {
          console.log(e)
        }
      })
    }
  }
}
</script>

