  .btnbox{
    overflow: hidden;
  }
	.line{
		display: block;
		position: absolute;
		width: 0.133rem;
		height: 0;
		border: 1px solid #444;
		left: 2.773rem;
    	top: 0.427rem;

	}
	*:focus {outline: none} 
    .orderSure{
    	height: 100%;
    }
	.box{
    border-bottom: none;
    margin-top: 1.173333rem;
		width: 100%;
		height: 13.3rem;
		position: absolute;
		box-sizing: border-box;
		padding: 0.36rem 0.533rem;
		padding-bottom: 127px;
		border-bottom: 1px solid #cccccc;
	}
	.box div{
		line-height: 0.453rem;
		height: 0.453rem;
	}
	div.address div{
		line-height: 0.88rem;
	}
	div.gangkou{
		width: 4.733rem;
		height: 0.933rem;
		text-align: left;
		padding-left: 0.133rem;
		box-sizing: border-box;
		border: 1px #aaa solid;
		border-radius: 0.08rem;
		position: relative;
	}
	i{
		height: 0.88rem;
		width: 0.453rem;
		background: url('icon_drop-down.png') no-repeat 0 center;
		background-size: 0.453rem 0.267rem;
		float: right;
		margin-right: 0.093rem;
	}
	div.gangkou p{
		line-height: 0.933rem
	}
	div.gangkou ul{
		width: 4.733rem;
		height: 4.667rem;
		position: absolute;
		left: -0.027rem;
