		margin-top: 1rem;
	}
	.checkbox{
		text-align: left;
		width: 4.5rem;
	}
	.checkbox p{
		float: left;
	}
	.yes,.no{
		width: 2.147rem;
		height: 0.72rem;
		line-height: 0.72rem;
