	}
	div.surebtn{
		width: 100%;
		height: 2.293333rem;
		bottom: 0.867rem;
		background: url('btn_Submit.png') no-repeat center;
		background-size: 9.6rem 2.293333rem;
	}
	div.btnbox{
    margin-top: 15rem;
		width: 100%;
		background-color: #f7f8fa;
		position: absolute;
	}
	div.total{
		line-height: 0.933rem;
		font-size: 0.6rem;
		color: #ed2818;
	}
</style>




