<template>
  <div class="order">
    <v-header :msg='msg' :re='re'></v-header>
    <div class="main">
    <div class="statistics"><span class="bluef">{{number}}</span><span class="bluef">{{quantity}}</span></div>
    <div class="pics">
      <schart :canvasId="canvasId"
            :type="type"
            :width="width"
            :height="height"
            :data="data"
            :options="options"
        ></schart>
    </div>
    <div id="totalnuml">
      <ul class="lefttext">
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <ul class="leftnum">
        <li>{{liferubbish}}</li>
        <li>{{cleanrubbish}}</li>
        <li>{{foodrubbish}}</li>
      </ul>
    </div>
    <div id="totalnumr">
      <ul class="righttext">
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <ul class="rightnum">
        <li>{{burnrubbish}}</li>
        <li>{{waterrubbish}}</li>
        <li>{{plastic}}</li>
      </ul>
    </div>
    <div class="bgc">
      <div id="comment">
      
    </div>
    <div class="clientcomment" v-for="result in result" @click='orderdetails(result)'>
      <div class="titleship">{{result.customerShipName}}</div>
      <div class="location">{{result.adress}}</div>
      <div class="myd"></div>
      <div class="star-wrap">
           <v-star :score='result.judge'></v-star>
      </div>
    </div>
    </div>
    </div>
        
  	<v-bottom></v-bottom>
</div>
</template>

<script>
import header from '@/components/Header/Header'
import bottom from '@/components/bottom/bottom'
import star from '@/components/star/star'
import Schart from 'vue-schart'
import $ from 'jquery'
export default {
  data () {
    return {
      msg: '',
      re: '3',
      number: '',
      quantity: '',
      liferubbish: '',
      cleanrubbish: '',
      foodrubbish: '',
      burnrubbish: '',
      waterrubbish: '',
      plastic: '',
      result: '',
      canvasId: 'myCanvas',
      type: 'line',
      width: 375,
      height: 170,
      data: [
      ],
      options: {
        padding: 40
      }
    }
  },
  components: {
    'v-header': header,
    'v-bottom': bottom,
    'v-star': star,
    Schart
  },
  mounted () {
    this.notepage()
  },
  methods: {
    notepage: function () {
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '004001', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {id: sessionStorage.getItem('id')}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          var listnum = (e.msgEx.infos)
          console.log(listnum)
          _this.number = listnum.times
          _this.quantity = listnum.amountGarbageForShow
          _this.liferubbish = listnum.life
          _this.cleanrubbish = listnum.sweeping
          _this.foodrubbish = listnum.foods
          _this.burnrubbish = listnum.burnCinder
          _this.waterrubbish = listnum.water
          _this.plastic = listnum.plastic
          _this.result = listnum.judgeList
          var weeklist = listnum.orderList
          var week1
          if (weeklist[0] === undefined) {
            week1 = '0'
          } else {
            week1 = weeklist[0].amountGarbageForShow
          }
          var week2
          if (weeklist[1] === undefined) {
            week2 = '0'
          } else {
            week2 = weeklist[1].amountGarbageForShow
          }
          var week3
          if (weeklist[2] === undefined) {
            week3 = '0'
          } else {
            week3 = weeklist[2].amountGarbageForShow
          }
          var week4
          if (weeklist[3] === undefined) {
            week4 = '0'
          } else {
            week4 = weeklist[3].amountGarbageForShow
          }
          var week5
          if (weeklist[4] === undefined) {
            week5 = '0'
          } else {
            week5 = weeklist[4].amountGarbageForShow
          }
          _this.data = [{name: '', value: week1}, {name: '', value: week2}, {name: '', value: week3}, {name: '', value: week4}, {name: '', value: week5}]
        },
        error: function (e) {
          console.log(e)
        }
      })
    },
    orderdetails: function (result) {
      console.log(result)
      sessionStorage.setItem('customerShipName', result.customerShipName)
      sessionStorage.setItem('adress', result.adress)
      sessionStorage.setItem('time', result.workDate)
      sessionStorage.setItem('customerShipId', result.customerShipId)
      sessionStorage.setItem('taskCode', result.taskCode)
      sessionStorage.setItem('taskType', result.taskType)
      if (result.ifType === '0') {
        sessionStorage.setItem('ifType', '')
      } else {
        sessionStorage.setItem('ifType', '')
      }
      if (result.eBurnCinder === '') {
        sessionStorage.setItem('eBurnCinder', '-')
      } else {
        sessionStorage.setItem('eBurnCinder', result.eBurnCinder)
      }
      if (result.eFood === '') {
        sessionStorage.setItem('eFood', '-')
      } else {
        sessionStorage.setItem('eFood', result.eFood)
      }
      if (result.eLife === '') {
        sessionStorage.setItem('eLife', '-')
      } else {
        sessionStorage.setItem('eLife', result.eLife)
      }
      if (result.ePlastic === '') {
        sessionStorage.setItem('ePlastic', '-')
      } else {
        sessionStorage.setItem('ePlastic', result.ePlastic)
      }
      if (result.eSweeping === '') {
        sessionStorage.setItem('eSweeping', '-')
      } else {
        sessionStorage.setItem('eSweeping', result.eSweeping)
      }
      if (result.eWater === '') {
        sessionStorage.setItem('eWater', '-')
      } else {
        sessionStorage.setItem('eWater', result.eWater)
      }
      if (result.fBurnCinder === '') {
        sessionStorage.setItem('fBurnCinder', '0')
      } else {
        sessionStorage.setItem('fBurnCinder', result.fBurnCinder)
      }
      if (result.fFood === '') {
        sessionStorage.setItem('fFood', '0')
      } else {
        sessionStorage.setItem('fFood', result.fFood)
      }
      if (result.fLife === '') {
        sessionStorage.setItem('fLife', '0')
      } else {
        sessionStorage.setItem('fLife', result.fLife)
      }
      if (result.fPlastic === '') {
        sessionStorage.setItem('fPlastic', '0')
      } else {
        sessionStorage.setItem('fPlastic', result.fPlastic)
      }
      if (result.fSweeping === '') {
        sessionStorage.setItem('fSweeping', '0')
      } else {
        sessionStorage.setItem('fSweeping', result.fSweeping)
      }
      if (result.fWater === '') {
        sessionStorage.setItem('fWater', '0')
      } else {
        sessionStorage.setItem('fWater', result.fWater)
      }
      if (result.amountGarbageForShow === '') {
        sessionStorage.setItem('amountGarbageForShow', '0')
      } else {
        sessionStorage.setItem('amountGarbageForShow', result.amountGarbageForShow)
      }
      if (result.amountGarbage === '') {
        sessionStorage.setItem('amountGarbage', '-')
      } else {
        sessionStorage.setItem('amountGarbage', result.amountGarbage)
      }
      if (result.eGungo === '') {
        sessionStorage.setItem('eGungo', '-')
      } else {
        sessionStorage.setItem('eGungo', result.eGungo)
      }
      if (result.fGungo === '') {
        sessionStorage.setItem('fGungo', '0')
      } else {
        sessionStorage.setItem('fGungo', result.fGungo)
      }
      this.$router.push({ path: '/orderfinish' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.statistics{
  margin-top: 44px;
  text-align: left;
  padding-left: 0.666667rem;
  font-size: 0.346667rem;
  color:#444444;
  height: 0.666667rem;
  line-height: 0.666667rem;
  background: #f7f8fa;
  border-bottom: 1px solid #dddddd;
}
.bluef{
  color: #2296f2;
}
.pics{
  height: 4.186667rem;
  overflow: hidden;
  background: #ffffff;
  width: 100%;
  border-bottom: 1px solid #eeeeee;
}

#totalnuml{
  height: 2.133333rem;
  margin-top: 0.053333rem;
  width: 50%;
  float: left;
  border-bottom: 1px solid #eeeeee;

}

.lefttext{
  margin-left: 0.32rem;
  font-size: 0.32rem;
  color: #444444;
  float: left;
  text-align: left;
}

.lefttext li{
  margin-top: 0.266667rem;
  background: url(icon_spot.png) no-repeat;
  background-position-y: center;
  padding-left: 0.266667rem;
}

.leftnum{
  font-size: 0.32rem;
  color: #666666;
  float: right;
  text-align: right;
  padding-right: 0.45rem;
}

.leftnum li{
  margin-top: 0.266667rem;
}

#totalnumr{
  height: 2.133333rem;
  margin-top: 0.053333rem;
  width: 50%;
  float: left;
  border-bottom: 1px solid #eeeeee;
}

.righttext{
  margin-left: 0.45rem;
  font-size: 0.32rem;
  color: #444444;
  float: left;
  text-align: left;
}

.righttext li{
  margin-top: 0.266667rem;
  background: url(icon_spot.png) no-repeat;
  background-position-y: center;
  padding-left: 0.266667rem;
}

.rightnum{
  font-size: 0.32rem;
  color: #666666;
  float: right;
  text-align: right;
  padding-right: 0.32rem;
}

.rightnum li{
  margin-top: 0.266667rem;
}
.bgc{
  width: 100%;
  height: 7.6rem;
  clear: both;
  background: #f7f8fa;
  overflow: auto;
}
#comment{
  clear: both;
  height: 0.666667rem;
  line-height: 0.8rem;
  font-size: 0.373333rem;
  color: #2296f2;
  background: url(icon_evaluate.png) no-repeat;
  background-position-y:0.186rem;
  background-position-x: 4.266667rem;
  padding-left: 1rem;
}
.clientcomment{
  width: 9.6rem;
  height: 1.733333rem;
  background: #ffffff;
  margin: 0 auto;
  margin-top: 0.133333rem;
  border: 1px solid #eeeeee;
}
.titleship{
  width: 50%;
  float: left;
  font-size: 0.453333rem;
  font-weight: bold;
  margin-top: 0.4rem;
  padding-left: 0.826667rem;
  text-align: left;
  background: url(icon_ship.png) no-repeat;
  background-position-x:0.293333rem;
}

.titleport{
  width: 50%;
  float: left;
  font-size: 0.453333rem;
  font-weight: bold;
  margin-top: 0.4rem;
  padding-left: 0.826667rem;
  text-align: left;
  background: url(icon_Port.png) no-repeat;
  background-position-x:0.293333rem;
}

.location{
  float: left;
  width: 50%;
  font-size: 0.346667rem;
  color: #444444;
  margin-top: 0.186667rem;
  text-align: left;
  padding-left: 0.826667rem;
}

.myd{
  margin-top: 0.426667rem;
  text-align: right;
  margin-right: 0.4rem;
  font-size: 0.32rem;
  color: #666666;
}
.star-wrap{
    float: right;
    margin-right: 0.15rem;
  }
</style>



