<template>
    <div class="center">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="banner">
        <p>{{username}}</p>
      </div>
      <div class="wrap">
        <div class="wrap1">
       
          <div class="phone">
            <div class="left phone1"></div>
            <div class="right">{{userphone}}</div>
          </div>
        </div>
        <router-link to="changepw">
        <div class="wrap2">
          <div class="password">
             <div class="left password1"></div>
             <div class="right"><i></i></div>             
          </div>
        </div>
        </router-link>

        <router-link to="note">
        <div class="wrap2">
          <div class="password">
             <div class="left note1"></div>
             <div class="right"><i></i></div>             
          </div>
        </div>
        </router-link>
        <router-link to="login">
          <div id="signout"></div>
        </router-link>
      </div>
      <v-bottom></v-bottom>
    </div>
</template>

<script>
  import header from '@/components/Header/Header'
  import bottom from '@/components/bottom/bottom'
  export default {
    data () {
      return {
        msg: '',
        re: '0',
        username: sessionStorage.getItem('loginname'),
        userphone: ''
      }
    },
    components: {
      'v-header': header,
      'v-bottom': bottom
    },
    mounted () {
      this.notepage()
    },
    methods: {
      notepage: function () {
        var phonenum = sessionStorage.getItem('phone')
        var mtel = phonenum.substr(0, 3) + '****' + phonenum.substr(7)
        this.userphone = mtel
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.banner{
  width: 100%;
  height: 3.2rem;
  background: url('images_kv.png') no-repeat;
  background-size: 10.0rem 3.2rem;
  overflow: hidden;
  margin-top: 44px;
}
.banner p{
  padding-left: 0.533333rem;
  height: 0.453rem;
  line-height: 0.47rem;
  font-size: 0.427rem;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  margin-top: 1.373333rem;
  background: url('icon_core_user.png') no-repeat;
  background-size: 0.4rem 0.453rem;
  background-position: 4rem center;
}
.wrap{
  width: 100%;
  height: 12.4rem;
  background: #F7F8FA
}


.left{
  float: left;
  line-height: 1.2rem;
  color: #666;
  padding-left: 0.8rem;
}
.right{
  float: right;
  line-height: 1.2rem;
  font-size: 0.4rem;
  color: #666;
}
.wrap1{
  width: 100%;
  padding-left: 0.533rem;
  padding-right: 0.533rem;
  box-sizing: border-box;
  height: 1.2rem;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  background: #fff;
}
.wrap2{
  width: 100%;
  padding-left: 0.533rem;
  padding-right: 0.533rem;
  box-sizing: border-box;
  height: 1.2rem;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  background: #fff;
  margin-top: 0.133rem;
}
.wrap1 div{
  font-size: 0.453rem;
  line-height: 1.2rem;
}
.wrap2 div{
  font-size: 0.453rem;
  line-height: 1.2rem;
}
i{
  float: right;
  width: 0.36rem;
  height: 0.64rem;
  background: url('open.fw.png');
  background-size: 0.36rem 0.64rem;
  margin-top: 0.267rem;
}

.phone1{
 background: url('icon-phone.png') no-repeat;
 background-position: 0 center;
 background-size: 0.667rem 0.667rem;    
}
.password1{
 background: url('icon_pw.png') no-repeat;
 background-position: 0 center;
 background-size: 0.667rem 0.667rem;    
}
.note1{
 background: url('icon_Notice.png') no-repeat;
 background-position: 0 center;
 background-size: 0.667rem 0.667rem;    
}
#signout{
  background: url(btn_Sign-out.png) no-repeat;
  background-size: 10.0rem 1.253333rem;
  margin-top: 0.666667rem;
  height: 1.253333rem;
  width: 10.0rem;
}
</style>



