<template>
  <div class="orderDetail">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="wrap">
        <div class="wrap1">
          <div class="address">
            <p class="left"></p>
            <p class="right"></p>
          </div>
          <div class="address">
            <p class="left"></p>
            <p class="right" id="gkname">{{adress}}</p>
          </div>
          <div class="address">
            <p class="left"></p>
            <p class="right">{{time}}</p>
          </div>
          <div class="address telbox">
            <p class="left"></p>
            <p class="right tel">13865785412</p>
          </div>
        </div>

          <div class="title">
            <p class="title1"></p>
          </div>
        <div class="wrap2">
          <div class="address">
            <p class="left"></p>
            <p class="right">{{ifType}}</p>
          </div>

          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eLife}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eWater}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eSweeping}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eFood}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eBurnCinder}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{ePlastic}}</span></p>
          </div>
          <div class="address">
            <p class="left">()</p>
            <p class="right"><span>{{eGungo}}</span></p>
          </div>

          <div class="address total" style="display: none">
            <p class="left">()</p>
            <p class="right">{{total}}</p>
          </div>
        </div>
      <div id="startup"  @click='startwork'></div> 
      </div>
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import $ from 'jquery'
export default {
  data () {
    return {
      msg: '',
      re: '6',
      adress: '',
      time: '',
      ifType: '',
      eBurnCinder: '',
      eFood: '',
      eLife: '',
      ePlastic: '',
      eSweeping: '',
      eWater: '',
      eGungo: ''
    }
  },
  components: {
    'v-header': header
  },
  computed: {
    total: function () {
      let sum = 0
      if (this.a > 0) {
        sum += this.a
      } else {
        sum += this.A
      }
      if (this.b > 0) {
        sum += this.b
      } else {
        sum += this.B
      }
      if (this.c > 0) {
        sum += this.c
      } else {
        sum += this.C
      }
      if (this.d > 0) {
        sum += this.d
      } else {
        sum += this.D
      }
      if (this.e > 0) {
        sum += this.e
      } else {
        sum += this.E
      }
      if (this.f > 0) {
        sum += this.f
      } else {
        sum += this.F
      }
      if (isNaN(sum)) {
        sum = 0
      }
      return sum
    }
  },
  mounted () {
    this.details()
  },
  methods: {
    details: function () {
      var _this = this
      var customerShipName = sessionStorage.getItem('customerShipName')
      var adress = sessionStorage.getItem('adress')
      var time = sessionStorage.getItem('time')
      var ifType = sessionStorage.getItem('ifType')
      var eBurnCinder = sessionStorage.getItem('eBurnCinder')
      var eFood = sessionStorage.getItem('eFood')
      var eLife = sessionStorage.getItem('eLife')
      var ePlastic = sessionStorage.getItem('ePlastic')
      var eSweeping = sessionStorage.getItem('eSweeping')
      var eWater = sessionStorage.getItem('eWater')
      var eGungo = sessionStorage.getItem('eGungo')
      _this.adress = adress
      _this.time = time
      _this.msg = customerShipName
      _this.ifType = ifType
      _this.eBurnCinder = eBurnCinder
      _this.eFood = eFood
      _this.eLife = eLife
      _this.ePlastic = ePlastic
      _this.eSweeping = eSweeping
      _this.eWater = eWater
      _this.eGungo = eGungo
    },
    startwork: function () {
      var isf
      if (sessionStorage.getItem('ifType') === '') {
        isf = 1
      } else {
        isf = 0
      }
      var myDate = new Date()
      var str = ''
      str += myDate.getFullYear() + '-'
      if (myDate.getMonth() >= 0 && myDate.getMonth() <= 9) {
        str += '0'
      }
      str += myDate.getMonth() + 1 + '-'
      str += myDate.getDate() + ' '
      str += myDate.getHours() + ':'
      if (myDate.getMinutes() >= 0 && myDate.getMinutes() <= 9) {
        str += '0'
      }
      str += myDate.getMinutes() + ':'
      if (myDate.getSeconds() >= 0 && myDate.getSeconds() <= 9) {
        str += '0'
      }
      str += myDate.getSeconds()
      console.log(str)
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '003005', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {id: sessionStorage.getItem('id'), userType: '2', taskCode: sessionStorage.getItem('taskCode'), customerShipId: sessionStorage.getItem('customerShipId'), workShipId: '', taskStatus: 2, taskType: sessionStorage.getItem('taskType'), workDate: str, ifType: isf, adress: $('#gkname').text()}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e)
          if (e.msgEx.respDesc === '!') {
            _this.$router.push({ path: '/overwork' })
          } else {
            alert(e.msgEx.respDesc)
          }
        },
        error: function (e) {
          console.log(e)
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .orderDetail{
    height: 100%;
    margin-top: 1.173333rem;
  }
  .wrap{
    background: #f7f8fa;
    height: 14.4rem;
    overflow: scroll;
  }
  .wrap1{
    width: 100%;
    padding-left: 0.533rem;
    padding-right: 0.533rem;
    border-bottom: 0.027rem #ccc solid;
    box-sizing: border-box;
    background: #fff;
  }
  .wrap2{
    width: 100%;
    padding-left: 0.533rem;
    padding-right: 0.533rem;
    border-top: 0.027rem #ccc solid;
    border-bottom: 0.027rem #ccc solid;
    box-sizing: border-box;
    background: #fff;
    margin-top: 0.267rem;
  }
  .wrap2 .address,.wrap2 .title{
    height: 1.2rem;
    line-height: 1.2rem;
  }
  .wrap2 .address p,.wrap2 .title p{
    height: 1.2rem;
    line-height: 1.2rem;
    line-height: 1.2rem;
  }
  .address{
    width: 100%;
    height: 1.2rem;
    border-bottom:1px solid #ccc; 
  }
  .title{
    width: 100%;
    height: 0.5rem;
  }
  .address .left{
    font-size: 0.453rem;
    float: left;
    line-height: 1.2rem;
  }
  .address .right{
    font-size: 0.4rem;
    float: right;
    line-height: 1.2rem;
    text-align: right;
  }
  .address .tel{
    color: #2296f2;
    font-size: 0.453rem;
    background: url('icon_list_Telephone.png') no-repeat;
    background-position: 0 center;
    background-size: 0.36rem 0.4rem;
    padding-left: 0.4rem;
  }
  .telbox{
    border-bottom: none;
  }
  .title1{
    background: url('icon_kind.png') no-repeat;
    background-size: 0.32rem 0.347rem;
    background-position-y: center;
    background-position-x: 0.533333rem;
    padding-right:6.0rem;
    font-size: 0.4rem;
    line-height: 0.75rem;
  }
  .total{
    border-bottom: none;
  }
  .total{
    color: #fc1b1b;
  }
  .total .right{
    font-size: 0.453rem;
  }
  #startup{
    background: url(btn_Start-up.png);
    background-size: 9.6rem 2.293333rem;
    height: 2.293333rem;
    width: 9.6rem;
    position: absolute;
    margin-top: -1.333333rem;
    margin-left: 0.2rem
  }
</style>



