<template>
    <div class="note">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="content" v-for="results in results">
        <div class="top">
          <div class="topleft"></div>
          <div class="toptime">{{results.createTime}}</div>
          <div class="topright"></div>
        </div>
        <div class="note1">
          <i></i>
          <div class="right">{{results.content}}</div>
        </div>
      </div>
    </div>
</template>

<script>
  import header from '@/components/Header/Header'
  import $ from 'jquery'
  export default {
    data () {
      return {
        msg: '',
        re: '1',
        results: ''
      }
    },
    components: {
      'v-header': header
    },
    mounted () {
      this.notepage()
    },
    methods: {
      notepage: function () {
        var _this = this
        $.ajax({
          type: 'post',
          url: '/smi-WSApp/Api/requestDeal',
          data: {arg0: '002001', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {pageNo: 1, pageSize: 20}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
          dataType: 'json',
          success: function (e) {
            var listnum = (e.msgEx.infos.results)
            console.log(listnum)
            _this.results = listnum
          },
          error: function (e) {
            console.log(e)
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello{
    position: fixed;
  }
  .note{
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: scroll;
  }
  .content{

    overflow: hidden;
    padding:0 0.2rem;
    box-sizing: border-box;
  }
  .content:nth-child(2){
     margin-top: 44px;   
  }
  .top{
    height: 0.907rem;
    position: relative;
  }
  .content .topleft{
    position: absolute;
    height: 0.427rem;
    width: 3.067rem;
    border-bottom: 1px solid #ccc;
    top: 0;
    left: 0;
  }
  .content .topright{
    position: absolute;
    height: 0.427rem;
    width: 3.067rem;
    border-bottom: 1px solid #ccc;
    right: 0;
    top: 0
  }
  .content .toptime{
    font-size: 0.32rem;
    color: #999;
    text-align: center;
    line-height: 0.907rem
  }

  .note1{
    width: 100%;
    position: relative;
    overflow: hidden;
    margin-top: 0.267rem;
  }
  .note1:nth-child(2){
    margin-top: 0;
  }
  .note1 i{
    background: url('note.fw.png');
    background-size: 0.667rem 0.667rem;
    width: 0.667rem;
    height: 0.667rem;
    float: left;
  }
  .note1 .right{
    font-size: 0.347rem;
    color: #444;
    width: 8.667rem;
    float: right;
    box-sizing: border-box;
    border: 1px #ccc solid;
    padding-top: 0.2rem;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
    padding-bottom: 0.4rem;
    text-align: left;
    line-height: 0.533rem;
    border-radius: 3px;
    position: relative;
  }
  .right:before, .right:after{
          width:0px;
          height:0px;
          border:transparent solid;
          position:absolute;
          left:-12px;
          content:""
        }
 .right:after{
    border-width:7px;
    border-right-color: #fff;
    top:5px;
  }
  .right:before{
    border-width:6px;
    border-right-color: #ccc;
    top:6px;
  }
</style>



