import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/Header/Header'
import bottom from '@/components/bottom/bottom'
import work from '@/components/work/work'
import orderSure from '@/components/orderSure/orderSure'
import index1 from '@/components/index1/index1'
import center from '@/components/center/center'
import orderDetail from '@/components/orderDetail/orderDetail'
import order from '@/components/order/order'
import login from '@/components/login/login'
import changepw from '@/components/changepw/changepw'
import note from '@/components/note/note'
import statisticslist from '@/components/statisticslist/statisticslist'
import overwork from '@/components/overwork/overwork'
import orderfinish from '@/components/orderfinish/orderfinish'
import createOrder from '@/components/createOrder/createOrder'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Header',
      component: Header
    },
    {
      path: '/',
      name: 'bottom',
      component: bottom
    },
    {
      path: '/work',
      name: 'work',
      component: work
    },
    {
      path: '/orderSure',
      name: 'orderSure',
      component: orderSure
    },
    {
      path: '/index1',
      name: 'index1',
      component: index1
    },
    {
      path: '/center',
      name: 'center',
      component: center
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/changepw',
      name: 'changepw',
      component: changepw
    },
    {
      path: '/note',
      name: 'note',
      component: note
    },
    {
      path: '/statisticslist',
      name: 'statisticslist',
      component: statisticslist
    },
    {
      path: '/overwork',
      name: 'overwork',
      component: overwork
    },
    {
      path: '/createOrder',
      name: 'createOrder',
      component: createOrder
    },
    {
      path: '/orderfinish',
      name: 'orderfinish',
      component: orderfinish
    },
    {
      path: '/orderDetail',
      name: 'orderDetail',
      component: orderDetail
    }
  ]
})



