<template>
  <div class="work">
      <v-header :msg='msg' :re='re'></v-header>
      <div class="w-box">
        <p></p>
        <div class="lajilei" v-bind:class="{'class-a':isA,'class-A':!isA}" @click="toggleA"><p></p></div>
        <div class="lajilei" v-bind:class="{'class-b':isB,'class-B':!isB}" @click="toggleB"><p></p></div>
        <div class="lajilei" v-bind:class="{'class-c':isC,'class-C':!isC}" @click="toggleC"><p></p></div>
        <div class="lajilei" v-bind:class="{'class-d':isD,'class-D':!isD}" @click="toggleD"><p></p></div>
        <div class="lajilei" v-bind:class="{'class-e':isE,'class-E':!isE}" @click="toggleE"><p></p></div>
        <div class="lajilei" v-bind:class="{'class-f':isF,'class-F':!isF}" @click="toggleF"><p></p></div>
        <router-link :to="{ path: 'orderSure', query: { plan: Number(isA)+''+Number(isB)+Number(isC)+Number(isD)+Number(isE)+Number(isF) }}" v-if='isA||isB||isC||isD||isE||isF'>
          <div class='sure'></div>
        </router-link>
        <div class="sure-x" v-else> </div>
      </div>
  </div>
</template>

<script>
import header from '@/components/Header/Header'
export default {
  data () {
    return {
      msg: '',
      re: '1',
      isA: false,
      isB: false,
      isC: false,
      isD: false,
      isE: false,
      isF: false
    }
  },
  components: {
    'v-header': header
  },
  methods: {
    toggleA: function () {
      this.isA = !this.isA
    },
    toggleB: function () {
      this.isB = !this.isB
    },
    toggleC: function () {
      this.isC = !this.isC
    },
    toggleD: function () {
      this.isD = !this.isD
    },
    toggleE: function () {
      this.isE = !this.isE
    },
    toggleF: function () {
      this.isF = !this.isF
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.work{

}
.w-box{
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: url('bg.png');
  background-size: 100% 100%;
  padding:0 0.41rem;
  box-sizing:border-box;
  position: absolute;
  z-index: 100;
}
p{
  font-size: 15px;
  color: #5b6269;
  text-align: left;
  line-height: 15px;
  margin-top: 0.173rem;
  margin-bottom: 0.04rem;
  margin-left: 0.133rem
}
.w-box div{
  width: 4.32rem;
  height: 4.16rem;
  margin: 0.133rem;
  float: left;
}
.w-box div p{
  width: 4.32rem;
  text-align: center;
  font-size: 18px;
  margin: 0;
  margin-top: 108px;
}
.w-box .sure{
  width: 8.933rem;
  height: 1.333rem;
  background: url('btn_start.png');
  background-size: 8.933rem 1.333rem;
  margin-top: 0.267rem
}
.w-box .sure-x{
  width: 8.933rem;
  height: 1.333rem;
  background: url('btn_start_x.png');
  background-size: 8.933rem 1.333rem;
  margin-top: 0.267rem
}
.class-a p,.class-b p,.class-c p,.class-d p,.class-e p,.class-f p{
    color: #419efe;
}
.class-a{
  background: url('page_icon_garbage_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-A{
  background: url('page_icon_garbage.png');
  background-size: 4.32rem 4.16rem;
}
.class-b{
  background:  url('page_icon_sewage_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-B{
  background:  url('page_icon_sewage.png');
  background-size: 4.32rem 4.16rem;
}
.class-c{
  background:  url('page_icon_Cabin_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-C{
  background:  url('page_icon_Cabin.png');
  background-size: 4.32rem 4.16rem;
}
.class-d{
  background:  url('page_icon_food_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-D{
  background:  url('page_icon_food.png');
  background-size: 4.32rem 4.16rem;
}
.class-e{
  background:  url('page_icon_Ash_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-E{
  background:  url('page_icon_Ash.png');
  background-size: 4.32rem 4.16rem;
}
.class-f{
  background:  url('page_icon_Plastic_x.png');
  background-size: 4.32rem 4.16rem;
}
.class-F{
  background:  url('page_icon_Plastic.png');
  background-size: 4.32rem 4.16rem;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



