<template>
  <div class="alert" v-show='flag'>
    <div class="box" :class='{box1:btn=="1"}'>
      <div class="content1">{{msga}}</div>
      <div class="reason">
        <div class="tit">:</div>
        <span id="reason1" class="reasontext" @click='copy1'></span>
        <span id="reason2" class="reasontext" @click='copy2'></span>
        <span id="reason3" class="reasontext" @click='copy3'></span>
      </div>
      <textarea id="area"></textarea>
      <div class="btnbox">
        <div class="btnleft" v-if='btn==0' @click='corder'></div>
        <div class="btnleft1" v-else @click='corder'></div>
        <div class="btnright" v-on:click='cancel'></div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  props: {
    flag: {
      type: Number
    },
    msga: {
      type: String
    },
    tel: {
      type: String
    },
    btn: {
      type: String
    }
  },
  data: function () {
    return {
      flag1: this.flag
    }
  },
  created: function () {
  },
  computed: {
  },
  methods: {
    cancel: function () {
      this.$emit('cancel')
      $('#area').val('')
    },
    copy1: function () {
      $('#area').val($('#area').val() + $('#reason1').text())
    },
    copy2: function () {
      $('#area').val($('#area').val() + $('#reason2').text())
    },
    copy3: function () {
      $('#area').val($('#area').val() + $('#reason3').text())
    },
    corder: function () {
      var _this = this
      if ($('#area').val() === '') {
        alert('')
        return
      }
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '003006', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {taskCode: sessionStorage.getItem('taskCode'), userType: '2', reason: $('#area').val()}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e.msgEx.respDesc)
          console.log(e)
          if (e.msgEx.respDesc === '!') {
            _this.$router.push({ path: '/index1' })
          } else {
            alert(e.msgEx.respDesc)
          }
        },
        error: function (e) {
          console.log(e)
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .alert{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    position: fixed;
    top: 0;
    z-index: 10000;
  }
  .box{
    margin: 0 auto;
    margin-top: 4.933rem;
    width: 7.867rem;
    height: 7.9rem;
    background: #fff;
    border-radius: 3px;
    overflow: hidden;
  }
  .content1{
    height: 1.733333rem;
    font-size: 0.48rem;
    color: #2296f2;
    font-weight: bold;
    text-align: center;
    line-height: 1.733333rem;
    border-bottom: 1px solid #cccccc;
  }
  .btnbox{
    clear: both;
    height: 0.907rem;
    width: 100%;
  }
  .btnleft{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_confirm.png');
    background-size: 3.6rem 1.2rem;
    margin-left: 0.213rem;
    float: left;
  }
  .btnleft1{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_confirm.png');
    background-size: 3.6rem 1.2rem;
    margin-left: 0.213rem;
    float: left;
  }
  .btnright{
    width: 3.6rem;
    height: 1.2rem;
    background: url('btn_cancel.png');
    background-size: 3.6rem 1.2rem;
    margin-right: 0.213rem;
    float: right;
  }
  .tit{
    width: 7.867rem;
    font-size: 0.373333rem;
    color: #444444;
    text-align: left;
    margin-left: 0.666667rem;
  }
  .reasontext{
    font-size: 0.293333rem;
    color: #444444;
    background-color: #dfdfdf;
    height: 0.426667rem;
    margin-left: 0.346667rem;
    padding: 0 0.2rem;
    position: relative;
    top: -0.533333rem;
    right: 0.16rem;
  }
  #area{
    width: 6.933333rem;
    height: 2.0rem;
    border: 1px solid;
    border-color: #cccccc;
    margin-top: -0.453333rem;
  }
</style>



