<template>
  <div class="work">
      <v-header :msg='msg' :re='re'></v-header>
       <div id="loginbg">
       </div>
       <div id="inputarea">
          <input type="password" id="oldPassword" class="password" name="password" placeholder=""></br>
          <input type="password" id="newPassword" class="password" name="password" placeholder=""></br>
          <input type="password" id="newPassword1" class="password" name="password" placeholder="">
       </div>
       <div id="conform" @click='pwconf'></div>
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import $ from 'jquery'
import md5 from 'js-md5'
export default {
  data () {
    return {
      msg: '',
      re: '1'
    }
  },
  components: {
    'v-header': header
  },
  methods: {
    pwconf: function () {
      var _this = this
      if ($('#newPassword1').val() === '') {
        alert('')
        return
      }
      if ($('#newPassword').val() !== $('#newPassword1').val()) {
        alert('')
      } else {
        $.ajax({
          type: 'post',
          url: '/smi-WSApp/Api/requestDeal',
          data: {arg0: '001003', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {jno: sessionStorage.getItem('jno'), oldPassword: md5($('#oldPassword').val()), newPassword: md5($('#newPassword').val())}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
          dataType: 'json',
          success: function (e) {
            console.log(e.msgEx.respDesc)
            console.log(e)
            if (e.msgEx.respDesc === '') {
              alert('')
              _this.$router.push({ path: '/login' })
            } else {
              alert(e.msgEx.respDesc)
            }
          },
          error: function (e) {
            console.log(e)
          }
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#loginbg{
  background: url(login_bg.png) no-repeat;
  background-size: 10.0rem 17.786667rem;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  z-index: -1;
}

#inputarea{
  text-align: center;
  margin-top: 4.026667rem
}
.password{
  margin-top: 0.533333rem;
  width: 6.133333rem;
  height: 1.2rem;
  border:none;
  padding-left: 1.2rem;
  font-size: 0.4rem;
  color: #666666;
  background: url(icon_Password.png) no-repeat;
  background-position-x: 0.24rem;
  background-position-y: center;
  background-color: #ffffff;
}
#conform{
  background: url(btn_Determine.png) no-repeat;
  background-size:7.333333rem 1.466667rem;
  height: 1.466667rem;
  width: 7.333333rem;
  margin: 0 auto;
  margin-top: 1.066667rem
}
</style>



