<template>
  <div class="star">
    <span v-for='itemClass in itemClasses' :class='itemClass' class="star-item"></span>
  </div>
</template>

<script>
const aa = 'on'
const bb = 'off'
export default {
  props: {
    score: {
      type: Number
    }
  },
  computed: {
    itemClasses () {
      let result = []
      for (let i = 0; i < this.score; i++) {
        result.push(aa)
      }
      while (result.length < 5) {
        result.push(bb)
      }
      return result
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.star{
		font-size: 0;
		width: 3.5rem;
		height: 0.64rem;
	}
	.star-item{
		width: 0.533rem;
		height: 0.507rem;
		margin: 0.067rem;
		float: left;
	}
	.on{
		background: url('Order-list_Stars02.png');
		background-size: 0.533333rem 0.506667rem;
	}
	.off{
		background: url('Order-list_Stars01.png');
		background-size: 0.533333rem 0.506667rem;
	}
</style>



