<template>
  <div class="orderSure">
    <v-header :msg='msg' :re='re'></v-header>
    <div class="box">
    <div class="address">
        <div class="left">/</div>
        <div class="right gangkou">
          <p @click='toggle1'>{{cbn1}} <i></i></p>
          <ul v-show='tog1'>
            <li v-for='(item1,index1) in cbn' dataid='1' @click='chose1($event,index1)'>{{item1}}</li>
          </ul>
        </div>
      </div>
    	<div class="address" style="margin-top:0.666667rem">
        <div class="left"></div>
        <div class="right gangkou">
          <input type="text" id="gkname" name="">
        </div>
      </div>
      <div class="address" style="margin-top:0.666667rem">
        <div class="left"></div>
        <div class="right gangkou">
          <p @click='toggle2'>{{tt1}} <i></i></p>
          <ul v-show='tog2'>
            <li v-for='(item2,index2) in tt' dataid='1' @click='chose2($event,index2)'>{{item2}}</li>
          </ul>
        </div>
      </div>
      <div class="address" style="margin-top:0.666667rem">
        <div class="left"></div>
        <div class="right gangkou">
          <p  @click='toggle3'>{{wbn1}}<i></i></p>
          <ul v-show='tog3'>
            <li v-for='(item3,index3) in wbn' dataid='1' @click='chose3($event,index3)'>{{item3}}</li>
          </ul>
        </div>
      </div>
    	<div class="country isdone">
    		<div class="left"></div>
    		<div class="right checkbox">
    			<p style='float:left' id="yesb" @click='change1' class="no"></p>
    			<p style='float:left'  id="nob" @click='change2' class="no"></p>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='0' @click='add($event)'></div>
    			<input id="flife" class="icenter" type="Number" name="" v-model="A">
    			<div class="iright" data='0' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='1' @click='add($event)'></div>
    			<input id="fWater" class="icenter" type="Number" name="" v-model="B">
    			<div class="iright" data='1' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='2' @click='add($event)'></div>
    			<input id="fSweeping" class="icenter" type="Number" name="" v-model="C">
    			<div class="iright" data='2' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='3' @click='add($event)'></div>
    			<input id="fFood" class="icenter" type="Number" name="" v-model="D">
    			<div class="iright" data='3' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right sh1"> 
    			<div class="ileft" data='4' @click='add($event)'></div>
    			<input id="fBurnCinder" class="icenter" type="Number" name="" v-model="E">
    			<div class="iright" data='4' @click='sub($event)'></div>
    		</div>
    	</div>
    	<div class="sh">
        <div class="left">()</div>
        <div class="right sh1"> 
          <div class="ileft" data='5' @click='add($event)'></div>
          <input id="fPlastic" class="icenter" type="Number" name="" v-model="F">
          <div class="iright" data='5' @click='sub($event)'></div>
        </div>
      </div>
      <div class="sh">
        <div class="left">()</div>
        <div class="right sh1"> 
          <div class="ileft" data='6' @click='add($event)'></div>
          <input id="fGungo" class="icenter" type="Number" name="" v-model="G">
          <div class="iright" data='6' @click='sub($event)'></div>
        </div>
      </div>
    	<div class="sh">
    		<div class="left">()</div>
    		<div class="right total"> 
    			{{Math.floor((A+B+C+D+E+F+G) * 10) / 10}}
    		</div>
    	</div>
    <span id="amountGarbage" style="display:none">{{Math.floor((A+B+C+D+E+F+G) * 10) / 10}}</span>
    </div>
    <div class="btnbox">
    	<div class="surebtn" v-if="flag" @click='subcreat'></div>
      <div class="surebtn" v-else></div>
    </div>
    
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import $ from 'jquery'
var tasktype = ['', '', '', '']
var shiparr
var shiparrindex
var addr
var addrindex
var stypeindex
var wshiparr
var wshiparrindex
export default {
  data () {
    return {
      msg: '',
      re: '1',
      id: '',
      gk: '',
      tog: false,
      gk1: '',
      tt: tasktype,
      tog2: false,
      tog1: false,
      tog3: false,
      flag: true,
      tt1: tasktype[0],
      wbn: '',
      wbn1: '',
      cbn: '',
      cbn1: '',
      A: 0,
      B: 0,
      C: 0,
      D: 0,
      E: 0,
      F: 0,
      G: 0
    }
  },
  components: {
    'v-header': header
  },
  mounted () {
    this.searchA()
    this.searchBN()
  },
  watch: {
    A: function () {
      if (this.A === 0) {
      } else {
        this.A = Math.floor(this.A * 10) / 10
      }
    },
    B: function () {
      if (this.B === 0) {
      } else {
        this.B = Math.floor(this.B * 10) / 10
      }
    },
    C: function () {
      if (this.C === 0) {
      } else {
        this.C = Math.floor(this.C * 10) / 10
      }
    },
    D: function () {
      if (this.D === 0) {
      } else {
        this.D = Math.floor(this.D * 10) / 10
      }
    },
    E: function () {
      if (this.E === 0) {
      } else {
        this.E = Math.floor(this.E * 10) / 10
      }
    },
    F: function () {
      if (this.F === 0) {
      } else {
        this.F = Math.floor(this.F * 10) / 10
      }
    },
    G: function () {
      if (this.G === 0) {
      } else {
        this.G = Math.floor(this.G * 10) / 10
      }
    }
  },
  methods: {
    toggle: function () {
      this.tog = !this.tog
    },
    chose: function (e, index) {
      console.log(index)
      addrindex = index
      this.gk1 = e.currentTarget.innerHTML
      this.tog = !this.tog
    },
    toggle1: function () {
      this.tog1 = !this.tog1
    },
    chose1: function (e, index1) {
      console.log(index1)
      shiparrindex = index1
      this.cbn1 = e.currentTarget.innerHTML
      this.tog1 = !this.tog1
    },
    toggle2: function () {
      this.tog2 = !this.tog2
    },
    chose2: function (e, index2) {
      stypeindex = index2
      this.tt1 = e.currentTarget.innerHTML
      this.tog2 = !this.tog2
    },
    toggle3: function () {
      this.tog3 = !this.tog3
    },
    chose3: function (e, index3) {
      wshiparrindex = index3
      this.wbn1 = e.currentTarget.innerHTML
      this.tog3 = !this.tog3
    },
    change1: function () {
      $('#yesb').addClass('yes')
      $('#yesb').removeClass('no')
      $('#nob').removeClass('yes')
      $('#nob').addClass('no')
    },
    change2: function () {
      $('#nob').addClass('yes')
      $('#nob').removeClass('no')
      $('#yesb').removeClass('yes')
      $('#yesb').addClass('no')
    },
    add: function (e) {
      var _this = e.currentTarget
      var aa = Number(_this.getAttribute('data'))
      if (aa === 0) {
        this.A = Number(this.A) + 0.5
      } else if (aa === 1) {
        this.B = Number(this.B) + 0.5
      } else if (aa === 2) {
        this.C = Number(this.C) + 0.5
      } else if (aa === 3) {
        this.D = Number(this.D) + 0.5
      } else if (aa === 4) {
        this.E = Number(this.E) + 0.5
      } else if (aa === 5) {
        this.F = Number(this.F) + 0.5
      } else if (aa === 6) {
        this.G = Number(this.G) + 0.5
      }
    },
    sub: function (e) {
      var _this = e.currentTarget
      var aa = Number(_this.getAttribute('data'))
      if (aa === 0 && this.A > 0) {
        this.A = Number(this.A) - 0.5
      } else if (aa === 1 && this.B > 0) {
        this.B = Number(this.B) - 0.5
      } else if (aa === 2 && this.C > 0) {
        this.C = Number(this.C) - 0.5
      } else if (aa === 3 && this.D > 0) {
        this.D = Number(this.D) - 0.5
      } else if (aa === 4 && this.E > 0) {
        this.E = Number(this.E) - 0.5
      } else if (aa === 5 && this.F > 0) {
        this.F = Number(this.F) - 0.5
      } else if (aa === 6 && this.G > 0) {
        this.G = Number(this.G) - 0.5
      }
    },
    searchA: function () {
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '005001', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {dataType: 'WS_ADDRESS'}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          var listnum = (e.msgEx.infos)
          console.log(listnum)
          var str = ''
          for (var i = listnum.length - 1; i >= 0; i--) {
            str += listnum[i].dataName
            str += ','
          }
          console.log(str)
          var arr1 = str.split(',')
          var arr2 = arr1.splice(0, arr1.length - 1)
          console.log(arr2)
          addr = listnum
          _this.gk = arr2
        },
        error: function (e) {
          console.log(e)
        }
      })
    },
    searchBN: function () {
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '005002', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {id: sessionStorage.getItem('id'), userType: '2'}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e.msgEx.infos)
          var ship = e.msgEx.infos
          var str = ''
          for (var j = ship.workShipList.length - 1; j >= 0; j--) {
            str += ship.workShipList[j].workShipName
            str += ','
          }
          var arr1 = str.split(',')
          var arr2 = arr1.splice(0, arr1.length - 1)
          console.log(arr2)
          wshiparr = ship.workShipList
          _this.wbn = arr2
          var str1 = ''
          for (var i = ship.customerShipList.length - 1; i >= 0; i--) {
            str1 += ship.customerShipList[i].customerShipName
            str1 += ','
          }
          var arr3 = str1.split(',')
          var arr4 = arr3.splice(0, arr3.length - 1)
          shiparr = ship.customerShipList
          _this.cbn = arr4
        },
        error: function (e) {
          console.log(e)
        }
      })
    },
    subcreat: function () {
      var _this = this
      if ($('#amountGarbage').text() === '0') {
        alert('0')
        return
      }
      console.log(shiparr)
      console.log(shiparrindex)
      console.log(addr)
      console.log(addrindex)
      console.log(stypeindex)
      console.log(wshiparr)
      console.log(wshiparrindex)
      console.log($('.yes').text())
      var isf
      if ($('.yes').text() === '') {
        alert('')
        return
      }
      if ($('.yes').text() === '') {
        isf = 1
      } else {
        isf = 0
      }
      if (addrindex === undefined) {
        addrindex = 0
      }
      if (shiparrindex === undefined) {
        alert('/')
        return
      }
      if (stypeindex === undefined) {
        stypeindex = 0
      }
      var myDate = new Date()
      var str = ''
      str += myDate.getFullYear() + '-'
      if (myDate.getMonth() >= 0 && myDate.getMonth() <= 9) {
        str += '0'
      }
      str += myDate.getMonth() + 1 + '-'
      if (myDate.getDate() >= 0 && myDate.getDate() <= 9) {
        str += '0'
      }
      str += myDate.getDate() + ' '
      if (myDate.getHours() >= 0 && myDate.getHours() <= 9) {
        str += '0'
      }
      str += myDate.getHours() + ':'
      if (myDate.getMinutes() >= 0 && myDate.getMinutes() <= 9) {
        str += '0'
      }
      str += myDate.getMinutes() + ':'
      if (myDate.getSeconds() >= 0 && myDate.getSeconds() <= 9) {
        str += '0'
      }
      str += myDate.getSeconds()
      console.log(str)
      _this.flag = false
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '003001', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {id: sessionStorage.getItem('id'), userType: '2', taskCode: null, customerShipId: shiparr.reverse()[shiparrindex].customerShipId, workShipId: wshiparr.reverse()[wshiparrindex].workShipId, taskStatus: 4, taskType: stypeindex, adress: $('#gkname').val(), ifType: isf, fLife: $('#flife').val(), fSweeping: $('#fSweeping').val(), fFood: $('#fFood').val(), fBurnCinder: $('#fBurnCinder').val(), fPlastic: $('#fPlastic').val(), fWater: $('#fWater').val(), fGungo: $('#fGungo').val(), workDate: str, amountGarbageForShow: $('#amountGarbage').text(), createTime: str}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e)
          _this.flag = true
          if (e.msgEx.respDesc === '!') {
            alert('!')
            _this.$router.push({ path: '/statisticslist' })
          } else {
            alert(e.msgEx.respDesc)
          }
        },
        error: function (e) {
          console.log(e)
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .btnbox{
    overflow: hidden;
  }
	.line{
		display: block;
		position: absolute;
		width: 0.133rem;
		height: 0;
		border: 1px solid #444;
		left: 2.773rem;
    	top: 0.427rem;

	}
	*:focus {outline: none} 
    .orderSure{
    	height: 100%;
    }
	.box{
    margin-top: 1.173333rem;
		width: 100%;
    overflow-y: scroll;
    border-bottom: none;
    height: 14rem;
		position: absolute;
    z-index: 111;
		box-sizing: border-box;
		padding: 0.36rem 0.533rem;
		padding-bottom: 127px;
		border-bottom: 1px solid #cccccc;
	}
	.box div{
		line-height: 0.453rem;
		height: 0.453rem;
	}
	div.address div{
		line-height: 0.88rem;
	}
	div.gangkou{
		width: 4.733rem;
		height: 0.933rem;
		text-align: left;
		padding-left: 0.133rem;
		box-sizing: border-box;
		border: 1px #aaa solid;
		border-radius: 0.08rem;
		position: relative;
	}
  #gkname{
    width: 4.5rem;
  }
	i{
		height: 0.88rem;
		width: 0.453rem;
		background: url('icon_drop-down.png') no-repeat 0 center;
		background-size: 0.453rem 0.267rem;
		float: right;
		margin-right: 0.093rem;
	}
	div.gangkou p{
		line-height: 0.933rem
	}
	div.gangkou ul{
		width: 4.733rem;
		height: 4.667rem;
    overflow: scroll;
		position: absolute;
		left: -0.027rem;
		top: 0.88rem;
		border: 1px #aaa solid;
		border-radius: 0.08rem;
		box-sizing: border-box;
		padding-left: 0.133rem;
		background: #fff;
		z-index: 1000;
	}
	div.gangkou ul li{
		height: 0.933rem;
	}
	.left{
		float: left;
		font-size: 0.453rem;
		text-align: left;
		color: #444;
	}
	.right{
		float: right;
		font-size: 0.4rem;
		text-align: right;
		color: #666;
	}
	.isdone{
		margin-top: 1rem;
	}
	.checkbox{
		text-align: left;
		width: 4.5rem;
	}
	.checkbox p{
		float: left;
	}
	.yes,.no{
		width: 2.147rem;
		height: 0.72rem;
		line-height: 0.72rem;
		margin-top: -0.133rem;
		box-sizing: border-box;
		padding-left: 0.64rem;
	}
	.yes{
		background: url('yes.fw.png');
		background-size: 2.147rem 0.72rem;
	}
	.no{
		background: url('no.fw.png');
		background-size: 2.147rem 0.72rem;
	}
	div.sh{
		height: 0.933rem;
		margin-top:0.373rem;
		
	}
	div.sh .left{
		line-height: 0.933rem;
	}
	.sh .sh1{
		width: 4.5rem;
		height: 0.933rem;
		border: 1px solid #ccccce;
		border-radius: 2px;
	}
	.sh .ileft{
		width:  0.933rem;
		height: 0.933rem;
		float: left;
		background: url('+.png');
		background-size: 0.933rem 0.933rem;
		border-right: 1px solid #ccc;
	}
	.sh .iright{
		width:  0.933rem;
		height: 0.933rem;
		float: right;
		background: url('-.png');
		background-size: 0.933rem 0.933rem;
		border-left: 1px solid #ccc;
	}
	.sh .icenter{
		width:  2.55rem;
		height: 0.933rem;
		float: left;
		text-align: center;
		line-height: 0.933rem;
	}
	div.surebtn{
		width: 100%;
    height: 2.293333rem;
    bottom: 0.867rem;
    background: url('btn_Submit.png') no-repeat center;
    background-size: 9.6rem 2.293333rem;
	}
	div.btnbox{
		margin-top: 14.8rem;
    width: 100%;
    background-color: #f7f8fa;
    position: absolute;
    z-index: 111;
	}
	div.total{
		line-height: 0.933rem;
		font-size: 0.6rem;
		color: #ed2818;
	}
</style>




