<template>
  <div class="work">
      <v-header :msg='msg' :re='re'></v-header>
       <div id="loginbg">
       </div>
       <div id="inputarea">
          <input type="text" id="phonenum" name="phonenum" placeholder=""></br>
          <input type="password" id="password" name="password" placeholder="">
       </div>
       <span id="forgetpw"  @click='show'></span>
       <hr id="line" />
       <div id="conform" @click='loginconf'></div>
       <span class="tishi">{{Msg}}</span>   
    <v-alert :flag='flag' :msga='msga' :tel='tel' btn='1' v-on:cancel='Pcancel'></v-alert>
  </div>
</template>

<script>
import header from '@/components/Header/Header'
import alert from '@/components/alert/alert'
import $ from 'jquery'
import md5 from 'js-md5'
export default {
  data () {
    return {
      msg: '',
      re: '0',
      flag: 0,
      Msg: '',
      msga: '',
      tel: '13817484102',
      loginname: ''
    }
  },
  components: {
    'v-header': header,
    'v-alert': alert
  },
  methods: {
    show: function () {
      this.flag = 1
    },
    Pcancel: function () {
      this.flag = 0
    },
    loginconf: function () {
      console.log($('#phonenum').val())
      var _this = this
      $.ajax({
        type: 'post',
        url: '/smi-WSApp/Api/requestDeal',
        data: {arg0: '001001', arg1: JSON.stringify({projectNo: 'smi', reqUrl: '', reqParam: {jno: $('#phonenum').val(), password: md5($('#password').val())}, reqHeadParam: {sessionToken: '', version: '1.0.0', mechanism: '', platform: 'web', togatherType: '', openchannel: '', token: '', userAgent: 'web'}, reqTimestamp: '123', sn: 'smi2017081117026884820cc06'}), arg2: '123456'},
        dataType: 'json',
        success: function (e) {
          console.log(e.msgEx.respDesc)
          console.log(e)
          sessionStorage.setItem('loginname', e.msgEx.infos.name)
          sessionStorage.setItem('id', e.msgEx.infos.id)
          sessionStorage.setItem('phone', e.msgEx.infos.phone)
          sessionStorage.setItem('jno', e.msgEx.infos.jno)
          if (e.msgEx.respDesc === '') {
            _this.$router.push({ path: '/index1' })
          } else {
            _this.Msg = e.msgEx.respDesc
            $('#phonenum').val('')
            $('#password').val('')
          }
        },
        error: function (e) {
          console.log(e)
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#loginbg{
  background: url(login_bg.png) no-repeat;
  background-size: 10.0rem 17.786667rem;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  z-index: -1;
}

#inputarea{
  text-align: center;
  margin-top: 1.173333rem;
}
#phonenum{
  position: relative;
  top: 3.386667rem;
  width: 6.133333rem;
  height: 1.2rem;
  margin: 0 auto;
  z-index: 1;
  border:none;
  padding-left: 1.2rem;
  font-size: 0.4rem;
  color: #666666;
  background: url(icon_user.png) no-repeat;
  background-position-x: 0.24rem;
  background-position-y: center;
  background-color: #ffffff;
}

#password{
  position: relative;
  top: 4.0rem;
  width: 6.133333rem;
  height: 1.2rem;
  margin: 0 auto;
  z-index: 1;
  border:none;
  padding-left: 1.2rem;
  font-size: 0.4rem;
  color: #666666;
  background: url(icon_Password.png) no-repeat;
  background-position-x: 0.24rem;
  background-position-y: center;
  background-color: #ffffff;
}

#forgetpw{
  font-size: 0.4rem;
  color: #ffffff;
  position: relative;
  top: 3.866667rem;
  left: 2.666667rem;

}

#line{
  height:1px;
  border:none;
  border-top:1px solid #ffffff;
  width: 1.75rem;
  position: relative;
  top: 3.36rem;
  left: 2.666667rem;
}

#conform{
  background: url(btn_Determine.png) no-repeat;
  background-size:7.333333rem 1.466667rem;
  height: 1.466667rem;
  width: 7.333333rem;
  margin: 0 auto;
  margin-top: 4.106667rem;
}
.tishi{
  margin-top: 10px;
  font-size: 12px;
  color: red;
  float: right;
  margin-right: 50px;
}
</style>



